import { useRef, type ReactNode, forwardRef, useContext } from "react"
import { useTabContext } from "./useTabContext"

import { css, cx } from "jh-generated/css"
import { RovingTabindexRoot, useRovingTabindex } from "./useRovingTabIndex"

interface TabListProps {
  children: ReactNode
  className?: string
}

const setIndicatorStyle = (target: HTMLElement) => {
  const targetRect = target.getBoundingClientRect()
  const parentRect = target.parentElement?.getBoundingClientRect()
  if (!targetRect || !parentRect) {
    return
  }
  document.documentElement.style.setProperty(
    "--indicator-left",
    `${Math.abs(parentRect.left - targetRect.left)}px`,
  )
  document.documentElement.style.setProperty(
    "--indicator-width",
    `${Math.abs(targetRect.width)}px`,
  )
}

export const TabList = ({ children, className }: TabListProps) => {
  const { selected, onSelect } = useTabContext("tab")

  return (
    <RovingTabindexRoot
      as="div"
      role="tablist"
      active={selected === undefined ? null : selected}
    >
      <div
        role="tablist"
        // onKeyDown={(e) => {
        //   handleKeyDown(e)
        // }}
        // onFocus={(e) => {
        //   const target = refs.current.find((node) => node === e.target)
        //   onSelect?.(target?.dataset.id)
        // }}
        className={cx(
          className,
          css({ position: "relative", display: "flex" }),
        )}
      >
        {children}
      </div>
    </RovingTabindexRoot>
  )
}

interface TabItemProps {
  children: ReactNode
  className?: string
  value: string
  asChild?: boolean
  disabled?: boolean
}

export const TabItem = forwardRef<any, TabItemProps>(
  ({ children, className, value, asChild }: TabItemProps, forwardRef) => {
    const { selected, onSelect, tabId } = useTabContext("tab")
    const isSelected = selected === value
    const { getOrderedItems, isFocusable, getRovingProps } =
      useRovingTabindex(value)
    console.log(isFocusable)
    return (
      <button
        {...getRovingProps()}
        role="tab"
        className={className}
        aria-selected={isSelected}
        tabIndex={isSelected ? 0 : -1}
        onClick={(e) => {
          e.preventDefault()
          onSelect?.(value)
          setIndicatorStyle(e.currentTarget)
        }}
        aria-controls={tabId + "-tabpanel-" + value}
        data-id={value}
        data-tab-item={""}
      >
        {children}
      </button>
    )
  },
)
